import CIcon from '@coreui/icons-react'
import {
  CBadge, CButton,

  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CCollapse,
  CDataTable,
  CRow
} from '@coreui/react'
import React from 'react'

const CombinedTable = ({name, fields, deleteItem, tableData, ...otherProps}) => {

  let [collapsed, setCollapsed] = React.useState(false);

  const deleteRow = (rowIndex) =>{
    deleteItem(rowIndex);
  }

  return (
    <>
        <CRow>
            <CCol>
              <CCard>
                <CCardHeader>
                  {name}
                  <div className="card-header-actions">
                        <CButton 
                        color="link" 
                        className="card-header-action btn-minimize" 
                        onClick={() => setCollapsed(!collapsed)}
                        >
                        <CIcon name={collapsed ? 'cil-chevron-bottom':'cil-chevron-top'} />
                        </CButton>
                    </div>
                </CCardHeader>
                <CCollapse show={collapsed} timeout={1000}>
                  <CCardBody>
                    <CDataTable
                    items={tableData}
                    fields={fields}
                    hover
                    striped
                    bordered
                    size="sm"
                    itemsPerPage={10}
                    pagination
                    /* scopedSlots = {{
                        'status':
                        (item)=>(
                            <td>
                            <CBadge color={getBadge(item.status)}>
                                {item.status}
                            </CBadge>
                            </td>
                        )
                    }} */
                    />
                  </CCardBody>
                </CCollapse>
              </CCard>
            </CCol>
        </CRow>
    </>
  )
}

export default CombinedTable
