import React from 'react';
import CombinedTable from './combined-table.component'

const PhoneTable = ({itemArray, deleteItem, ...otherProps}) => {
    const fields = ['tph_number', 'tph_communication_type', 'tph_contact_type', 'tph_country_prefix', 'tph_extension', 'comments'];
    var tableData =[];

    if(itemArray){
        console.log("itemPhone: ", itemArray);
        tableData = itemArray;
    }

    return(
        <CombinedTable name="Phone Table" fields={fields} tableData={tableData} deleteItem={deleteItem} />
    )
}

export default PhoneTable;